        boxShadow: isOpen
            ? '0 0 40px rgba(239, 68, 68, 0.6), 0 0 80px rgba(239, 68, 68, 0.3), inset 0 0 20px rgba(255,255,255,0.2)'
            : '0 0 40px rgba(16, 185, 129, 0.6), 0 0 80px rgba(16, 185, 129, 0.3), inset 0 0 20px rgba(255,255,255,0.2)',
        cursor: 'pointer',
        zIndex: 200,
